<template>
  <div id="app">
    <div>   
     <select @change="nameChange('selectedSpec',$event)">
     <option value="">--请选择--</option>
        <option :value ="item" v-for="item in specs" :key="item">{{item}}</option>
      </select>
       <select @change="nameChange('selectedColor',$event)">
        <option :value ="item" v-for="item in colors" :key="item">{{item}}</option>
      </select>
      <button @click="filterProd">结算</button>
    </div>

  
    <table class="product-table" cellpadding="0" cellspacing="0">
      <tr class="border-bt"><td colspan="8" class="title"><span style="font-size: 14px; ">爱墅美彩铝落水系统价格表</span></td></tr>
      <tr class="border-bt">
        <th>序号</th>
        <th>产品名称</th>
        <th>单位</th>
        <th>数量</th>
        <th>单价</th>
        <th>金额</th>
        <th>颜色</th>
      </tr>
      <tr class="border-bt" v-for="(item,index) in tableData" :key="item.id">
        <td>{{index + 1}}</td>
        <td>
	<input v-if='flag' type="text" placeholder="产品名称" :value="item.prodName" class="z4-input" @input="handleChange(index,$event,'name')" >
	<span v-else>{{item.prodName}}</span>
	</td>   
	<td>
	<input v-if='flag' type="text" placeholder="单位" :value="item.unit" class="z1-input" @input="handleChange(index,$event,'unit')" >
	<span v-else>{{item.unit}}</span>
	</td>
        <td>
          <input v-if='flag' type="number" placeholder="数量" :value="item.account" class="z2-input" @input="handleChange(index,$event,'num')" >
	<span v-else>{{item.account}}</span>
        </td>
        <td><input v-if='flag' type="text" placeholder="价格" :value="item.price" class="z3-input" @input="handleChange(index,$event,'pri')" >
	<span v-else>{{item.price}}</span>
	</td>
        <td><span class="r-input">{{(item.account * (+item.price)).toFixed(2)}}</span></td>
        <td ref="menuItem"  :rowspan="tableData.length+tableData2.length+1" v-if="index==0" class="shu">
	<input v-if='flag' type="text" placeholder="颜色" :value="selectedColor" class="z2-input" @input="handleChange(index,$event,'color')" >
	<span v-else>{{selectedColor}}</span>
	</td>
      </tr>
      <tr v-if="twoFlag" class="border-bt"><td colspan="6" class="title"><span style="font-size: 14px; ">雨水管及配件</span></td></tr>
      <tr class="border-bt" v-for="(item,index) in tableData2" :key="item.id">
        <td>{{index + tableData.length+1}}</td>
        <td>
  <input v-if='flag' type="text" placeholder="产品名称" :value="item.prodName" class="z4-input" @input="handleChange2(index,$event,'name')" >
  <span v-else>{{item.prodName}}</span>
  </td>   
  <td>
  <input v-if='flag' type="text" placeholder="单位" :value="item.unit" class="z1-input" @input="handleChange2(index,$event,'unit')" >
  <span v-else>{{item.unit}}</span>
  </td>
        <td>
          <input v-if='flag' type="number" placeholder="数量" :value="item.account" class="z2-input" @input="handleChange2(index,$event,'num')" >
  <span v-else>{{item.account}}</span>
        </td>
        <td><input v-if='flag' type="text" placeholder="价格" :value="item.price" class="z3-input" @input="handleChange2(index,$event,'pri')" >
  <span v-else>{{item.price}}</span>
  </td>
        <td><span class="r-input">{{(item.account * (+item.price)).toFixed(2)}}</span></td>
        <td ref="menuItem" :rowspan="tableData.length+tableData2.length" v-if="index+tableData.length==0 " class="shu">
  <input v-if='flag' type="text" placeholder="颜色" :value="selectedColor" class="z2-input" @input="handleChange(index,$event,'color')" >
  <span v-else>{{selectedColor}}</span>
  </td>

      </tr>
      <tr class="border-bt"><td colspan="8" class="total">总价：<span class="r-input">{{totalPrice.toFixed(2)}}</span>元（
<input v-if='flag' type="text" placeholder="税金" :value="test" class="z5-input" @input="handleChange(index,$event,'test')" >
	<span class="r-input" v-else>{{test}}</span>)
      <br>
      负责人：15857135106 章真庆
      </td>
      </tr>
    </table>
    <button v-if='flag' class="addBut" @click="addProd"><img style="width:20px;height:20px" src="add.png"/></button>
  </div>
</template>

<script>
import responseData from './data'
import responseData2 from './data2'

export default {
  name: 'App',
  data() {
    return {
      selectedSpec: "",
      selectedColor: "咖啡色",
      test:"不含运费，不含税金",
      specs: ["5寸","6寸","7寸","8寸","型材"],
      colors: ["咖啡色","白色","灰色","米黄"],
      tableData: [],
      tableData2: [],
      productList: responseData.data,
      productList2: responseData2.data,
      flag:false,
      twoFlag:false
    }
  },
  mounted:function(){
  var e = {};
  e.target.value = "5寸";
  this.nameChange("selectedSpec", e);
  },
  computed: {
    totalPrice() {
      var total = 0
      this.tableData.forEach(val => {
        total += val.account * val.price
      })
      this.tableData2.forEach(val => {
        total += val.account * val.price
      })
      return total
    }
  },
  methods: {
    handleChange(index, e,col) {
    var obj = this.tableData[index]
    var val = e.target.value 
     if(col === 'num'){
	if (val) {
        obj.account = +val
      } else {
        obj.account = val
      }
     }else if(col === 'pri'){
	obj.price = val
     }else if(col === 'name'){
	obj.prodName = val
     }else if(col === 'color'){
	this.selectedColor = val
     }else if(col === 'spec'){
	obj.spec = val
     }else if(col === 'unit'){
	obj.unit = val
     }else if(col === 'test'){
      this.test = val
     }
      this.$set(this.tableData, index, obj)
    },
    handleChange2(index, e,col) {
    var obj = this.tableData2[index]
    var val = e.target.value 
     if(col === 'num'){
  if (val) {
        obj.account = +val
      } else {
        obj.account = val
      }
     }else if(col === 'pri'){
  obj.price = val
     }else if(col === 'name'){
  obj.prodName = val
     }else if(col === 'color'){
  this.selectedColor = val
     }else if(col === 'spec'){
  obj.spec = val
     }else if(col === 'unit'){
  obj.unit = val
     }else if(col === 'test'){
      this.test = val
     }
      this.$set(this.tableData2, index, obj)
    },
    filterProd(){
      this.flag=false
      this.tableData = this.tableData.filter(val => !!val.account)
      this.tableData2 = this.tableData2.filter(val => !!val.account)
      if(this.tableData2.length === 0){
        this.twoFlag = false
      }else{
      this.twoFlag = true
      }
    
    },
    addProd() {
    this.flag=true
    var newobj={};  
        newobj.unit = "";
	newobj.prodName = "";
	newobj.price = "";
	!newobj.account && (newobj.account = 0)
        this.tableData2.push(newobj)
        var newobjk={};  
        newobjk.unit = "";
  newobjk.prodName = "";
  newobjk.price = "";
  !newobjk.account && (newobjk.account = 0)
        this.tableData.push(newobjk)
    
    },
    nameChange(name, e) {
	this[name] = e.target.value
      if(name === 'selectedSpec'){
    if (!this.selectedSpec) {
        alert('请选择规格！')
        return ;
      }
      var list = this.productList.filter(prod => {
      var flag2 = this.selectedSpec ? (prod.spec === this.selectedSpec) : true
        return flag2
      })
       this.tableData.splice(0,this.tableData.length)
	this.flag=true
      if (list.length === 0) {
        alert('无匹配项')
      } else {
        list.forEach(val => {
	val.data.forEach(valuea => {
	var newobj={};  
        newobj.unit = valuea.unit;
	newobj.prodName = valuea.prodName;
	newobj.price = valuea.price;
	!newobj.account && (newobj.account = 0)
        this.tableData.push(newobj)
	})
        })


var list2 = this.productList2.filter(prod => {
      var flag3 = this.selectedSpec ? (prod.spec === this.selectedSpec) : true
        return flag3
      })
       this.tableData2.splice(0,this.tableData2.length)
  this.flag=true
      if (list2.length === 0) {
        alert('无匹配项')
      } else {
        list2.forEach(val => {
  val.data.forEach(valuea => {
  var newobjj={};  
        newobjj.unit = valuea.unit;
  newobjj.prodName = valuea.prodName;
  newobjj.price = valuea.price;
  !newobjj.account && (newobjj.account = 0)
        this.tableData2.push(newobjj)
  })
        })}


  this.twoFlag = true
	this.flag=true
	this.test = '不含运费，不含税金'
      }
      }
    }

  }
}
</script>

