.total {
  font-weight: bold;
}
.shu{ 
width: 14px; 
font-size: 14px; 
word-wrap: break-word; 
letter-spacing: 0px;
}
.addBut{
padding:0px 0px;
width: 23px;
height: 23px;
background: #ff000000;
